import React from 'react';

function Content(props) {
    
    const contentContainerStyle = {
        position: "relative",
        display: "block",
        width: "80vmin",
        height: "auto",
        backgroundColor: "white",
        boxShadow: "0 4px 15px 0 rgba(0, 0, 0, 0.75)",
        borderRadius: "10px",
        margin: "0 auto",
        padding: "0",
    }

    return (
       
            <div id={props.contentId} style={contentContainerStyle} className={props.contentClass}>
                {props.contentHeader}
                {props.contentTitle}
                {props.contentBody}
            </div>

    )
}

export default Content;