        width: "100%",
        height: "20vmin",
        borderTopLeftRadius: "inherit",
        borderTopRightRadius: "inherit",
        borderBottom: "none",
        backgroundColor: "#aacfd0",
        display: "flex",
        margin: "0",
        justifyContent: "center",
        alignItems: "center",
