import React, {useState} from 'react';

import Header from './Header';
import EnterButton from './EnterButton';

function Hello(props) {
    //Header
    const displayHeader = {
    }

    const hideHeader = {
        width: "0"
    }

    const [nameHover, setNameHover] = useState(false)
    const [titleHover, setTitleHover] = useState(false)
    const [headerStyle, setHeaderStyle] = useState(displayHeader)

    const nameStyles = {
        transform: nameHover ? "translateX(-20vmin)" : "",
        transition: "transform 1000ms, opacity 1500ms",
        opacity: nameHover ? "0" : "1"
    }

    const titleStyles = {
        transform: titleHover ? "translateX(20vmin)" : "",
        transition: "transform 1000ms, opacity 1500ms",
        opacity: titleHover ? "0" : "1"
    }

    setTimeout(() => {
        setNameHover(true);
        setTitleHover(true)
    }, 1000) 
    
    setTimeout(() => {
        setHeaderStyle(hideHeader);
    }, 1500) 


    //Button
    const [btnHover, setBtnHover] = useState(false)
    


       
    const btnStyles = {
        backgroundColor: btnHover ? "#aacfd0" : "#f4f7f7",
        color: btnHover ? "#f4f7f7" : "",
        opacity: nameHover ? "1" : "0",
        transition: "opacity 1000ms",
    }

    const enterStyles = {
        backgroundColor: btnHover ? "#aacfd0" : "#f4f7f7",
    }
  
    function onBtnHover() {
        setBtnHover(true)
    }

    function offBtnHover() {
        setBtnHover(false)
    }


    return (
        <div className={props.helloClass}>
            <Header
                headerStyle={headerStyle}
                headerClass="header"
                headerName="header-name"
                headerTitle="header-title"
                name="forgerson"
                title=".DEV"
                nameStyle={nameStyles}
                titleStyle={titleStyles}
              />
            <EnterButton 
                btnStyle={btnStyles}
                btnClasses={props.btnClasses}
                btnOnClick={props.btnOnClick}
                btnOnHover={onBtnHover}
                btnOffHover={offBtnHover}
                enterClass={props.helloBtnClass}
                enterStyle={enterStyles}/>
        </div>
    )
}

export default Hello;