import React from 'react'

function Nav(props) {
   
    return (
        <div className={props.navContClass}>
            <div className="navInfoContainer">
                <i onClick={props.infoClick} className="fas fa-info-square"></i>
                <i onClick={props.signInClick} className="fal fa-sign-in"></i>
                <i onClick={props.questionInClick} className="fas fa-question-square"></i>
            </div>
           <div className="navSocialContainer">
                <i onClick={props.linkedinClick} className="fab fa-linkedin"></i>
                <i onClick={props.emailClick} className="fal fa-envelope"></i>
                <i onClick={props.instagramClick} className="fab fa-instagram-square"></i>
           </div>
        </div>
        
    )
}

export default Nav;