import React from 'react';

function CardBody(props) {

    const contentBodyStyle = {
        padding: "5%",
        margin: "0",
        width: "auto",
        height: "auto",
        fontSize: "calc(1rem + 0.5vmin)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
    }

    return (
       <div style={contentBodyStyle} className={props.bodyTextClass}>{props.text}</div>
    )
}

export default CardBody;